@import "variables.scss";

@font-face {
  font-family: 'rawline';
  src: url('../fonts/rawline-100.eot');
  src: url('../fonts/rawline-100.eot?#iefix') format('embedded-opentype'),
  url('../fonts/rawline-100.woff2') format('woff2'),
  url('../fonts//rawline-100.woff') format('woff'),
  url('../fonts/rawline-100.ttf') format('truetype'),
  url('../fonts//rawline-100.svg') format('svg');
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: 'rawline';
  src: url('../fonts/rawline-400.eot');
  src: url('../fonts/rawline-400.eot?#iefix') format('embedded-opentype'),
  url('../fonts/rawline-400.woff2') format('woff2'),
  url('../fonts//rawline-400.woff') format('woff'),
  url('../fonts/rawline-400.ttf') format('truetype'),
  url('../fonts//rawline-400.svg') format('svg');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'rawline';
  src: url('../fonts/rawline-500.eot');
  src: url('../fonts/rawline-500.eot?#iefix') format('embedded-opentype'),
  url('../fonts/rawline-500.woff2') format('woff2'),
  url('../fonts//rawline-500.woff') format('woff'),
  url('../fonts/rawline-500.ttf') format('truetype'),
  url('../fonts//rawline-500.svg') format('svg');
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: 'rawline';
  src: url('../fonts/rawline-600.eot');
  src: url('../fonts/rawline-600.eot?#iefix') format('embedded-opentype'),
  url('../fonts/rawline-600.woff2') format('woff2'),
  url('../fonts//rawline-600.woff') format('woff'),
  url('../fonts/rawline-600.ttf') format('truetype'),
  url('../fonts//rawline-600.svg') format('svg');
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: 'rawline';
  src: url('../fonts/rawline-700.eot');
  src: url('../fonts/rawline-700.eot?#iefix') format('embedded-opentype'),
  url('../fonts/rawline-700.woff2') format('woff2'),
  url('../fonts//rawline-700.woff') format('woff'),
  url('../fonts/rawline-700.ttf') format('truetype'),
  url('../fonts//rawline-700.svg') format('svg');
  font-weight: 700;
  font-style: normal;
}


* {
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
}

body {
  min-width: 320px;
  min-height: 100%;
  display: flex;
  flex-direction: column;

  font-family: $font-family;

  background: $black2;

  &.overfl {
    overflow: hidden;
    height: 100vh;
  }
}

.start-page {
  padding: 30px;

  &-logo {
    display: block;
    margin-bottom: 30px;
  }

  ul {
    display: inline-block;
    vertical-align: top;
    padding-right: 60px;

    li {
      padding: 5px 0;

      b {
        font-size: 20px;
      }

      a {
        font-size: 16px;
        line-height: 140%;
        color: $white;

        &:hover {
          //color: $active;
        }

        .red {
          color: red;
        }
      }
    }
  }
}

div,
span,
a,
input,
textarea,
select,
button,
.button {
  &:focus {
    outline: none;
  }
}

input,
textarea {
  box-shadow: none!important;
}


ul {
  margin: 0;
  padding: 0;

  li {
    list-style: none;
  }
}

a {
  cursor: pointer;
  transition: 0.3s;
  text-decoration: none;

  &:hover {
    text-decoration: none;
  }
}

svg {
  use {
    transition: 0.3s;
  }
}

.container {
  max-width: 1720px;
  padding: 0 20px;

  box-sizing: border-box;
  margin: auto;
}



h1 {
  font-weight: 600;
  font-size: 68px;
  line-height: 120%;
  color: $white;
  text-transform: uppercase;

  -ms-word-break: break-all;
  word-break: break-all;
  word-break: break-word;

  &.tw {
    font-size: 56px;
    line-height: 120%;

    hyphens: auto;
  }

  span {
    color: $red;
  }

  @media screen and(max-width: 991px) {
    font-size: 32px;
    line-height: 120%;

    .page-title & {
      font-size: 28px;
      line-height: 120%;
    }

    &.tw {
      font-size: 32px;
      line-height: 120%;
    }
  }
}

h2 {
  margin-bottom: 15px;

  color: $white;
  font-weight: 600;
  font-size: 56px;
  line-height: 120%;
  text-transform: uppercase;

  span {
    color: $red;
  }

  @media screen and(max-width: 991px) {
    margin-bottom: 10px;

    font-size: 28px;
    line-height: 120%;

    br {
      display: none;
    }
  }
}

h3 {
  font-weight: 500;
  font-size: 52px;
  line-height: 119.5%;
  color: $white;

  @media screen and (max-width: 991px) {
    font-size: 26px;
    line-height: 119.5%;
  }
}

.bt {
  background: transparent;
  border: none;
  box-shadow: none;
  outline: none;

  font-family: $font-family;

  &-primary {
    padding: 12px 28px;
    display: inline-flex;
    align-items: center;
    justify-content: center;

    border: 1px solid $white;
    border-radius: 27px;
    font-weight: 500;
    font-size: 16px;
    line-height: 119.5%;
    color: $white;

    box-sizing: border-box;

    @media screen and(max-width: 991px) {
      padding: 10px 20px;
      justify-content: space-between;

      font-size: 14px;
      line-height: 140%;
    }

    svg {
      width: 16px;
      height: 16px;
      margin-left: 10px;

      use {
        color: $white;
      }
    }

    &:hover {
      background: $red;
      border-color: $red;
    }
  }

  &-rd {
    padding: 12px 28px;
    display: inline-flex;
    align-items: center;
    justify-content: center;

    border: 1px solid $red;
    border-radius: 27px;
    font-weight: 500;
    font-size: 16px;
    line-height: 119.5%;
    color: $white;
    background: $red;

    box-sizing: border-box;

    @media screen and(max-width: 991px) {
      padding: 10px 20px;
      justify-content: space-between;

      font-size: 14px;
      line-height: 140%;
    }

    svg {
      width: 16px;
      height: 16px;
      margin-left: 10px;

      use {
        color: $white;
      }
    }

    &:hover {
      background: $white;
      border-color: $white;

      color: $black2;

      svg {
        use {
          color: $black2;
        }
      }
    }
  }
}

.mt-0 {
  margin-top: 0!important;
}

.header {
  padding: 12px 0;

  border-bottom: 1px solid $gray2;

  @media screen and(max-width: 991px) {
    padding: 8px 0;

    border: none;
  }

  .banner & {
    background: transparent;
    border-bottom: 1px solid $gray;

    @media screen and(max-width: 991px) {
      border: none;
    }
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    @media screen and(max-width: 991px) {
      position: relative;
      z-index: 999;
    }

    img {
      max-width: 131px;

      @media screen and(max-width: 991px) {
        max-width: 72px;
      }
    }
  }

  &__holder {
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media screen and(max-width: 1400px) {
      width: 80%;
    }
    @media screen and(max-width: 1250px) {
      width: 85%;
    }

    @media screen and(max-width: 1200px) {
      width: 86%;
    }

    @media screen and(max-width: 991px) {
      width: 100%;
      height: 100vh;
      min-height: 100%;
      padding: 90px 20px 200px;
      display: none;

      position: fixed;
      top: 0;
      left: 0;
      z-index: 99;

      background: $black2;
      overflow: auto;
      box-sizing: border-box;

      .open & {
        display: block;
      }

      &:before {
        content: '';
        width: 100%;
        height: 48px;
        display: block;

        position: fixed;
        top: 0;
        left: 0;
        z-index: 998;

        background: $black2;
      }
    }
  }

  &__toggl {
    display: none;
    width: 28px;
    height: 28px;
    align-items: center;
    justify-items: center;

    position: relative;
    z-index: 998;

    border: 1px solid $white;

    background-image: url("../images/svg/togl.svg");
    background-repeat: no-repeat;
    background-position: center;

    .open & {
      background-image: url("../images/svg/close.svg");
      background-size: 16px 16px;
    }

    @media screen and(max-width: 991px) {
      display: flex;
    }
  }

  &__mob {
    display: none;

    @media screen and(max-width: 991px) {
      display: block;
      padding: 17px 20px;
      width: 100%;

      position: fixed;
      bottom: 0;
      left: 0;
      z-index: 999;

      border-top: 1px solid $gray2;

      background: $black2;
    }
  }

  &__lk {
    display: inline-block;
    margin-top: 22px;

    font-size: 12px;
    line-height: 120%;
    color: $gray;
    text-decoration: underline;
  }

  &__soc {
    margin-top: 10px;

    li {
      display: inline-block;
      vertical-align: top;
      margin-right: 2px;

      a {
        width: 36px;
        height: 36px;
        display: flex;
        align-items: center;
        justify-content: center;

        border: 1px solid $white;

        svg {
          width: 20px;
          height: 20px;

          use {
            color: $white;
          }
        }
      }

    }
  }

  &__mail {
    display: inline-flex;
    align-items: center;

    img {
      margin-right: 5px;
    }
  }

  &__mail {
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    color: $white;
  }

  &__menu {
    ul {
      li {
        margin: 0 38px;
        display: inline-block;
        vertical-align: middle;

        @media screen and(max-width: 1500px) {
          margin: 0 20px;
        }

        @media screen and(max-width: 1200px) {
          margin: 0 10px;
        }

        @media screen and(max-width: 991px) {
          display: block;
          margin: 0 0 20px;
        }

        a {
          font-size: 16px;
          line-height: 140%;
          color: $white;

          &:hover {
            color: $red;
          }
        }

        &.active {
          a {
            color: $red;
          }
        }
      }
    }
  }

  &__btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 9px 14px;

    font-weight: 500;
    font-size: 16px;
    line-height: 119.5%;
    color: $white;
    border: 1px solid $white;
    border-radius: 27px;

    @media screen and(max-width: 991px) {
      display: none;
    }

    svg {
      width: 25px;
      height: 25px;
      margin-left: 8px;

      use {
        color: $white;
      }
    }

    &:hover {
      background: $red;
      border-color: $red;
    }
  }
}

.banner {
  height: 950px;

  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media screen and(max-width: 991px) {
    height: 480px;
  }

  h1 {
    span {
      display: block;
    }
  }

  &-main {
    height: calc(100% - 89px);

    @media screen and(max-width: 991px) {
      height: calc(100% - 52px);
    }

    .container {
      height: 100%;
    }

    &__inner {
      height: 100%;
      display: flex;
      align-items: flex-end;
    }

    &__content {
      width: 100%;
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      padding-bottom: 80px;

      @media screen and(max-width: 991px) {
        display: block;
        padding-bottom: 40px;
      }
    }

    &__text {
      width: 70%;

      @media screen and(max-width: 991px) {
        width: 100%;
      }

      p {
        margin-top: 30px;
        max-width: 400px;

        font-size: 28px;
        line-height: 124%;
        color: $white;

        @media screen and(max-width: 991px) {
          font-size: 18px;
          line-height: 124%;
          margin-top: 5px;

          br {
            display: none;
          }
        }
      }
    }

    &__soc {
      @media screen and(max-width: 991px) {
        margin-top: 20px;
      }

      ul {
        li {
          display: inline-block;
          vertical-align: top;
          margin-left: 6px;

          &:first-of-type {
            margin-left: 0;
          }

          a {
            width: 60px;
            height: 60px;
            display: flex;
            align-items: center;
            justify-content: center;

            border: 1px solid $white;

            @media screen and(max-width: 991px) {
              width: 36px;
              height: 36px;
            }

            svg {
              width: 32px;
              height: 32px;
              transition: 0.3s;

              @media screen and(max-width: 991px) {
                width: 20px;
                height: 20px;
              }

              use {
                color: $white;
              }
            }

            &:hover {
              svg {
                transform: scale(1.2);
              }
            }
          }
        }
      }
    }
  }
}

.spec {
  margin-top: 80px;

  @media screen and(max-width: 991px) {
    margin-top: 32px;
  }

  &__inner {
    display: flex;
    align-items: center;

    @media screen and(max-width: 991px) {
      display: block;
    }
  }

  &__img {
    width: 41%;

    @media screen and(max-width: 991px) {
      width: 100%;
    }

    img {
      max-width: 100%;
    }
  }

  &__info {
    width: 59%;
    padding-left: 70px;
    box-sizing: border-box;

    @media screen and(max-width: 1300px) {
      padding-left: 50px;
    }

    @media screen and(max-width: 991px) {
      width: 100%;
      padding-left: 0;
      margin-top: 15px;
    }

    p {
      font-size: 16px;
      line-height: 150%;
      color: $gray;

      @media screen and(max-width: 991px) {
        font-size: 14px;
      }
    }
  }

  &__tbl {
    margin-top: 20px;

    @media screen and(max-width: 991px) {
      margin-top: 5px;
    }

    &-row {
      padding: 40px 0;
      display: flex;
      align-items: flex-start;

      border-bottom: 1px solid $white;

      @media screen and(max-width: 991px) {
        display: block;
        padding: 15px 0;
      }
    }

    &-title {
      width: 45%;
      display: flex;
      align-items: center;
      padding-left: 45px;

      position: relative;

      font-weight: 500;
      font-size: 28px;
      line-height: 124%;
      color: $white;
      box-sizing: border-box;

      @media screen and(max-width: 991px) {
        width: 100%;
        padding-left: 28px;

        font-size: 18px;
        line-height: 124%;
      }

      &:before {
        content: '';
        width: 20px;
        height: 20px;
        display: block;
        margin-right: 24px;

        position: absolute;
        top: 6px;
        left: 0;

        background-image: url("../images/svg/arrow.svg");

        @media screen and(max-width: 991px) {
          width: 16px;
          height: 16px;

          top: 3px;

          background-size: cover;
        }
      }
    }

    &-tx {
      width: 55%;
      padding-left: 30px;

      box-sizing: border-box;

      font-size: 16px;
      line-height: 150%;
      color: $gray;

      @media screen and(max-width: 991px) {
        width: 100%;
        margin-top: 12px;
        padding-left: 0;

        font-size: 14px;
      }
    }
  }
}

.prgct {
  margin-top: 80px;

  @media screen and(max-width: 991px) {
    margin-top: 32px;
  }

  &-arrow-next,
  &-arrow-prev {
    @media screen and(max-width: 991px) {
      display: none;
    }
  }

  &__inner {
    position: relative;

    .swiper {
      @media screen and(max-width: 991px) {
        &-button {
          &-prev,
          &-next {
            top: 0;
            bottom: 0;
            margin: auto;

            background: transparent;

            &:after {
              content:  '';
              width: 100%;
              height: 100%;
              display: block;

              position: absolute;
              top: 0;
              left: 0;
              z-index: 1;

              background: rgba(255, 255, 255, 0.08);
              backdrop-filter: blur(4px);
              border-radius: 100px;
            }

            &:before {
              z-index: 5;
            }
          }

          &-prev {
            left: 0;
            right: inherit;
          }

          &-right {
            right: 0;
          }

          &-disabled {
            display: none;
          }
        }
      }
    }
  }

  &__slider {
    margin-top: 40px;

    @media screen and(max-width: 991px) {
      padding-bottom: 25px;
      margin: 0 -20px;

      overflow: hidden;
    }

    &-item {
      height: 700px;

      position: relative;

      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;

      @media screen and(max-width: 991px) {
        height: auto;
      }

      &:after {
        content: '';
        width: 100%;
        height: 100%;
        display: block;

        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;

        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 53.01%, rgba(0, 0, 0, 0.6) 80.67%), linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
      }
    }

    &-inner {
      padding: 56px;
      box-sizing: border-box;
      height: 100%;
      display: flex;
      align-items: flex-end;
      justify-content: space-between;

      position: relative;
      z-index: 5;

      @media screen and(max-width: 991px) {
        padding: 28px 20px;
        display: block;
      }
    }

    &-dt {
      display: inline-block;
      padding: 6px 16px;

      font-weight: 700;
      font-size: 12px;
      line-height: 119.5%;
      letter-spacing: 0.08em;
      color: $white;
      text-transform: uppercase;
      border: 1px solid $white;

      @media screen and(max-width: 991px) {
        padding: 4px 8px;

        font-size: 8px;
        line-height: 119.5%;
      }
    }

    &-title {
      margin-top: 5px;

      font-weight: 700;
      font-size: 52px;
      line-height: 119.5%;
      color: $white;

      @media screen and(max-width: 991px) {
        font-size: 24px;
        line-height: 119.5%;
      }
    }

    &-text {
      width: calc(100% - 175px);
      padding-right: 30px;

      box-sizing: border-box;

      @media screen and(max-width: 991px) {
        width: 100%;
        padding-right: 50px;
      }

      p {
        margin-top: 15px;

        font-size: 16px;
        line-height: 150%;
        color: $white;

        @media screen and(max-width: 991px) {
          margin-top: 6px;

          font-size: 14px;
          line-height: 140%;
        }
      }
    }

    &-btn {
      @media screen and(max-width: 991px) {
        margin-top: 12px;
      }
    }
  }
}

.tx {
  margin-top: 80px;

  @media screen and(max-width: 991px) {
    margin-top: 32px;
  }

  &__inner {
    display: flex;
    align-items: center;

    @media screen and(max-width: 991px) {
      flex-direction: column-reverse;
    }
  }

  &__text {
    width: 50%;
    padding-right: 70px;

    box-sizing: border-box;

    @media screen and(max-width: 1200px) {
      padding-right: 50px;
    }

    @media screen and(max-width: 991px) {
      width: 100%;
      padding-right: 0;
      margin-top: 13px;
    }

    p {
      margin-top: 20px;

      font-size: 16px;
      line-height: 150%;
      color: $gray;

      @media screen and(max-width: 991px) {
        font-size: 14px;
        line-height: 140%;
      }
    }
  }

  &__img {
    width: 50%;

    @media screen and(max-width: 991px) {
      width: 100%;
    }

    img {
      max-width: 100%;
    }
  }
}

.news {
  margin-top: 80px;

  @media screen and(max-width: 991px) {
    margin-top: 32px;
    padding-top: 32px;

    border-top: 1px solid $gray2;
  }

  &-page {
    overflow: hidden;

    &__inner {

    }

    &__row {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -33px -50px;

      @media screen and(max-width: 1200px) {
        margin: 0 -15px -50px;
      }

      @media screen and(max-width: 991px) {
        margin-bottom: -20px;
      }
    }

    &__cell {
      padding: 0 33px;
      margin-bottom: 50px;

      box-sizing: border-box;

      @media screen and(max-width: 1200px) {
        padding: 0 15px;
      }

      @media screen and(max-width: 991px) {
        margin-bottom: 20px;
      }

      &.w-1 {
        width: 33.333%;

        @media screen and(max-width: 991px) {
          width: 50%;
        }

        @media screen and(max-width: 768px) {
          width: 100%;
        }
      }
    }
  }

  &-item {
    &__inner {
      max-width: 830px;
      margin: auto;

      @media screen and(max-width: 991px) {
        max-width: 100%;
      }
    }

    &__banner {
      height: 600px;
      display: flex;
      align-items: center;
      justify-content: center;

      position: relative;

      overflow: hidden;

      @media screen and(max-width: 991px) {
        height: 170px;
      }


      &:before {
        content: '';
        width: 100%;
        height: 100%;
        display: block;

