.swiper {
  &-button {
    &-prev,
    &-next {
      width: 44px;
      height: 44px;

      top: 33px;
      bottom: inherit;
      right: 0;

      border-radius: 100px;
      background: $black3;
      transition: 0.3s;

      .team & {
        top: 55px;
        right: inherit;
        left: 0;
      }

      .about-history & {
        top: inherit;
        bottom: 20px;

        background: rgba(255, 255, 255, 0.2);
      }

      .team-item__sl & {
        background: rgba(255, 255, 255, 0.1);
        bottom: 0;
        right: 0;
        top: inherit;
      }

      .team-item__sl-slider & {
        @media screen and(max-width: 991px) {
          top: 0!important;
          bottom: 0!important;
          margin: auto!important;


          background: rgba(255, 255, 255, 0.1);
        }
      }

      &:after {
        display: none;
      }

      &:before {
        content: '';
        width: 20px;
        height: 20px;
        display: block;

        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;

        background-image: url("../images/svg/arrow2.svg");
        background-repeat: no-repeat;
        background-position: center;
      }

      &:hover {
        width: 56px;
        height: 56px;

        top: 27px;

        .team & {
          top: 50px;
        }

        .team-item__sl & {
          top: inherit;
          bottom: -7px;
        }

        .about-history & {
          top: inherit;
          bottom: 13px;
        }
      }
    }

    &-next {
      transform: rotate(180deg);

      .team & {
        left: 65px;
      }

      .about-history & {
        left: 65px;
      }

      .team-item__sl-slider & {
        @media screen and(max-width: 991px) {
          right: 10px;
          left: inherit;
        }
      }
    }

    &-prev {
      left: inherit;
      right: 65px;

      .team & {
        left: 0;
        right: inherit;
      }

      .about-history & {
        left: 0;
      }

      .team-item__sl & {
        right: 65px;
      }

      .team-item__sl-slider & {
        @media screen and(max-width: 991px) {
          left: 10px;
          right: inherit;
        }
      }
    }
  }

  &-pagination {
    text-align: left;
    bottom: 0!important;

    @media screen and(min-width: 991px) {
      .prgct & {
        display: none;
      }
    }

    &-bullet {
      width: 12px;
      height: 12px;

      position: relative;

      border: 1px solid $white;
      background: transparent;
      opacity: 1;

      box-sizing: border-box;

      &:before {
        content: '';
        width: 6px;
        height: 6px;

        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;

        border-radius: 100px;
        background: $white;
        opacity: 0;
        transition: 0.3s;
      }

      &-active {
        &:before {
          opacity: 1;
        }
      }
    }

    &-fraction {
      display: inline-flex;
      align-items: flex-end;
      justify-content: flex-end;

      font-size: 16px;
      line-height: 140%;
      color: $gray;

      .team-item__sl & {
        position: absolute;
        top: 0;
        right: 0;
        left: inherit!important;
        bottom: inherit!important;
        z-index: 4;

        @media screen and(max-width: 1200px) {
          justify-content: flex-start;
          bottom: 0!important;
          left: 20px!important;
          top: inherit;
          right: inherit;
        }

        @media screen and(max-width: 991px) {
          display: none!important;
        }
      }
    }

    &-current {
      display: inline-block;
      padding-right: 5px;
      font-weight: 600;
      font-size: 36px;
      line-height: 119.5%;
      color: $white;
    }

    &-total {
      position: relative;

      font-size: 16px;
      line-height: 140%;
      color: $gray;

      &:before {
        content: '0';
        display: inline-block;
      }
    }
  }
}

