.remodal {
  padding: 0;
  max-width: 808px;

  text-align: left;

  &.w-2 {
    max-width: 760px;
  }

  &-overlay {
    background: rgba(17,17,17, 0.6);
  }


  &__inner {
    padding: 55px;

    box-sizing: border-box;
    background: $black3;

    @media screen and(max-width: 991px) {
      padding: 20px;
    }

    .form {
      margin-top: 25px;

      @media screen and(max-width: 991px) {
        margin-top: 10px;
      }
    }
  }

  &__title {
    font-weight: 500;
    font-size: 52px;
    line-height: 119.5%;
    color: $white;

    @media screen and(max-width: 991px) {
      font-size: 26px;
      line-height: 119.5%;
    }
  }

  &__tx {
    margin-top: 20px;

    font-size: 16px;
    line-height: 140%;
    color: $white;

    @media screen and(max-width: 991px) {
      margin-top: 10px;

      font-size: 14px;
      line-height: 140%;
    }
  }

  &__btn {
    margin-top: 40px;

    @media screen and(max-width: 991px) {
      margin-top: 24px;
    }
  }
}

.remodal-close {
  top: -20px!important;
  right: -25px!important;
  left: inherit!important;
  width: 24px;
  height: 24px;

  background-image: url("../images/svg/close2.svg")!important;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain!important;
  color: transparent!important;

  @media screen and(max-width: 991px) {
    right: 0!important;
    top: -40px!important;
  }
}


.ck {
  width: 100%;
  padding: 36px 0;

  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 15;

  background: $black3;
  box-sizing: border-box;

  @media screen and(max-width: 991px) {
    padding: 16px 0;
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;

    max-width: 1050px;
    margin: auto;

    @media screen and(max-width: 991px) {
      display: block;
    }
  }

  &__tx {
    font-size: 20px;
    line-height: 140%;
    color: $white;

    @media screen and(max-width: 991px) {
      font-size: 12px;
      line-height: 140%;
    }

    span {
      color: $red;
    }
  }

  &__btn {
    display: inline-flex;
    align-items: center;

    @media screen and(max-width: 991px) {
      margin-top: 16px;
    }

    .bt {
      margin-left: 10px;

      @media screen and(max-width: 991px) {
        padding: 10px 20px;
        margin-left: 0;
        margin-right: 10px;
      }
    }
  }
}


.validation {
  & > span {
    position: absolute;
    left: 25px;
    bottom: -17px;

    font-size: 12px;
    line-height: 16px;
    color: red;
  }
}